<template>
  <!-- class markdown-body 必须加，否则标签样式会出现问题
  <div class="markdown-body">
    <markdown />
  </div>-->
  <div v-html="md" class = "markdown-body"></div>
</template>

<script class="hljs" >
  const marked = require('marked')
  import document from '../markdown/technology.md'
  // 代码高亮
  import 'highlight.js/styles/github.css'
  // 其他元素使用 github 的样式
  import 'github-markdown-css'

  export default {
    name :"Markdown_doc",
    data() {
      return {
        docName : "",
        md : ''
      }
    },
    created() {
      // 调用marked函数，传入helloMarked，返回一段html
      this.md = marked(document)
    },

  }
</script>